h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: Inter, sans-serif;
}

body.light-theme {
  color: #050505;
  background-color: #f8f8f8;
}

body.dark-theme {
  color: #fff;
  background-color: #161616;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 704px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1080px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

.section {
  padding-top: 80px;
  padding-bottom: 80px;
}

@media screen and (min-width: 1280px) {
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  gap: 16px;
  font-size: 12px;
  display: flex;
}

.nav-link {
  padding-top: 15px;
  padding-bottom: 15px;
  transition: text-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.nav-link:hover {
  text-shadow: 0 0 10px;
}

.current {
  color: #9bb537;
}

.header-logo {
  letter-spacing: -.045rem;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.25rem;
}

.header-logo-span {
  color: #9bb537;
}

.utility-bar {
  align-items: center;
  gap: 24px;
  display: flex;
}

.cart-button {
  line-height: 0;
}

.cart-icon {
  fill: rgba(0, 0, 0, 0);
  stroke: #050505;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

body.light-theme .cart-icon {
  stroke: #050505;
}

body.light-theme .cart-icon:hover {
  stroke: #9bb537;
}

body.dark-theme .cart-icon {
  stroke: #fff;
}

body.dark-theme .cart-icon:hover {
  stroke: #9bb537;
}

.switch {
  width: 46px;
  height: 20px;
  display: block;
  position: relative;
}

.checkbox {
  opacity: 0;
  height: 0;
  width: 0;
}

.slider {
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 50px;
  transition: all .5s ease-out;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

body.light-theme .slider {
  box-shadow: 8px 5px 15px rgba(0, 0, 0, .3);
}

body.dark-theme .slider {
  box-shadow: 8px 5px 15px rgba(255, 255, 255, .15);
}

.slider:before {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #f8f8f8;
  border: 1px solid #cecdcd;
  border-radius: 50%;
  transition: all .5s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.checkbox:checked + .slider {
  background-color: #9bb537;
}

.checkbox:checked + .slider:before {
  transform: translateX(26px);
}

.favorites-hero {
