body {
  font-family: $fontPrimary;
}

body.light-theme {
  background-color: lightColor('white-full');
  color: $black-full;
}

body.dark-theme {
  background-color: darkColor('dark-black');
  color: darkColor('white-full');
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
